.initialloc {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100vh;
}

.orbcursor {
  --orbsize: 10px;
  --growScale: 10;
  --offset: -.5px;

  display: none;
  position: fixed;
  width: var(--orbsize);
  height: var(--orbsize);
  background: white;
  top: 0;
  left: 0;
  mix-blend-mode: difference;
  z-index: 10;
  pointer-events: none;
  border-radius: 50%;
}

.animactive {
  animation: orbactive 0.5s forwards var(--sleek);
}

@keyframes orbactive {
  from {
    transform: scale(5);
  }

  to {
    transform: scale(1) translate(calc(var(--orbsize)/-2), calc(var(--orbsize)/-2));
  }
}

.animoff {
  animation: animoff 1s forwards var(--sleek);
}

@keyframes animoff {
  from {
    transform: scale(1) translate(calc(var(--orbsize)/-2), calc(var(--orbsize)/-2));
  }

  to {
    transform: scale(0);
  }
}

.hoveringattr {
  animation: hovering 0.5s forwards var(--sleek);
}

@keyframes hovering {
  from {
    transform: scale(1) translate(calc(var(--orbsize)/-2), calc(var(--orbsize)/-2));
  }

  to {
    transform: scale(var(--growScale)) translate(var(--offset), var(--offset));
  }
}

.idle {
  animation: idle 0.5s forwards var(--sleek);
}

@keyframes idle {
  from {
    transform: scale(var(--growScale)) translate(var(--offset), var(--offset));
  }

  to {
    transform: scale(1) translate(calc(var(--orbsize)/-2), calc(var(--orbsize)/-2));
  }
}

